.avatar {
  @apply font-semibold font-inter;
  @apply bg-surface;
  @apply overflow-hidden;
  @apply flex items-center justify-center;

  svg {
    @apply fill-on-surface;
  }

  &.circle {
    @apply rounded-1/2;
  }

  &.square {
    @apply rounded;
  }

  &.xxs {
    @apply w-24px;
    @apply h-24px;
    @apply text-caption-sm;
    svg {
      @apply w-21px;
      @apply h-21px;
    }
  }

  &.xs {
    @apply w-32px;
    @apply h-32px;
    @apply text-caption-lg;
    svg {
      @apply w-21px;
      @apply h-21px;
    }
  }

  &.sm {
    @apply w-40px;
    @apply h-40px;
    @apply text-body-sm;
  }

  &.md {
    @apply w-48px;
    @apply h-48px;
    @apply text-body-lg;
  }

  &.lg {
    @apply w-56px;
    @apply h-56px;
    @apply text-body-lg;
    svg {
      @apply w-43px;
      @apply h-43px;
    }
  }

  &.xl {
    @apply w-64px;
    @apply h-64px;
    @apply text-body-lg;
    svg {
      @apply w-43px;
      @apply h-43px;
    }
  }
}
