.badge {
  @apply font-semibold font-inter text-caption-sm;
  @apply whitespace-nowrap;
  @apply inline-flex items-center justify-center;
  @apply rounded-1/2;
  @apply w-6px;
  @apply h-6px;

  &.small {
    @apply w-18px;
    @apply h-18px;
  }

  &.large {
    @apply w-auto h-auto;
    @apply rounded-100px;
    @apply px-8px;
    @apply py-1px;
  }

  &.primary {
    @apply bg-primary;
    @apply text-on-primary;
  }

  &.secondary {
    @apply bg-secondary;
    @apply text-on-secondary;
  }

  &.tertiary {
    @apply bg-tertiary;
    @apply text-on-tertiary;
  }

  &.error {
    @apply bg-error;
    @apply text-on-error;
  }

  &.success {
    @apply bg-success;
    @apply text-on-success;
  }
}
