.button {
  @apply font-medium font-inter;
  @apply border rounded;
  @apply overflow-hidden;
  -webkit-tap-highlight-color: transparent;

  .state-layer {
    @apply px-1.8;
    @apply py-0.9;
    span {
      @apply flex items-center;
    }
  }

  &.large {
    @apply text-button-lg;
  }

  &.small {
    @apply text-button-sm;
    svg {
      @apply w-21px;
      @apply h-21px;
    }
  }

  &.outlined {
    &:not(:disabled) {
      &:hover {
        @apply border-2;
        .state-layer {
          @apply px-1.7375;
          @apply py-0.8375;
        }
      }
    }
  }

}
